@import url('https://fonts.googleapis.com/css?family=Roboto:300,900');

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*:focus {
  outline: none;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  background: #eee;
}

a {
  display: inline-block;
  background: #000;
  color: #fff;
  text-decoration: none;
  padding: 2px 8px;
  text-transform: uppercase;
  font-weight: 900;
  border-radius: 4px;
}

header, footer {
  text-align: center;
}

header {
  margin: 50px 0;
  text-transform: uppercase;
}

.App {
  width: 570px;
  background: #fff;
  margin: 0 auto;
  border-radius: 4px;
  padding: 30px;
  -webkit-box-shadow: 0px 0 18px -5px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0 18px -5px rgba(0, 0, 0, 0.25);
}

form {
  position: relative;
}

input {
  padding: 15px;
  border: solid 2px transparent;
  width: 100%;
  border-radius: 4px;
  -webkit-box-shadow: 0px 0 18px -5px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0 18px -5px rgba(0, 0, 0, 0.25);
  font-size: 22px;
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
}

.input-error {
  border: solid 2px #F44336;
}

form button {
  position: absolute;
  right: 0;
  background: #000;
  border: none;
  color: #fff;
  height: 100%;
  font-weight: 900;
  padding: 8px 30px;
  font-size: 28px;
  cursor: pointer;
}

.item-lists {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-top: 25px;
    border: solid 1px #eee;
}

.item-lists li {
    position: relative;
    padding: 20px;
    width: 100%;
    border-bottom: solid 1px #eee;
}

.item-lists li:hover button {
  opacity: 1;
}

.item-lists li:last-child {
    border-bottom: none;
}

.item-lists li button {
    position: absolute;
    top: 18px;
    right: 20px;
    background: #000;
    border: none;
    color: #fff;
    padding: 5px 10px;
    font-weight: 900;
    opacity: 0;
    cursor: pointer;
    -webkit-transition: 0.2s all;
    -o-transition: 0.2s all;
    transition: 0.2s all;
}

.item-lists li span {
    width: 420px;
    height: 20px;
    font-weight: 300;
    display: inline-block;
    overflow: hidden;
}

.item-lists li span.done {
    color: #B0BEC5;
    text-decoration: line-through;
}

footer {
  padding: 50px 0;
}

@media only screen and (max-width: 767px) {
  body {
    font-size: 16px;
  }
}



